import styled from 'styled-components'

export const Arrow = styled.i`
    border:${props => props.dark ? 'solid white': 'solid black'};
    border-width: 0 3px 3px 0;
    display: inline-block;
    padding: 3px;
    margin-right: 10px;
    padding-top: -10px;
    animation:${props => props.active ? 'blinker 1s linear infinite' : 'none'};
    opacity:0;
    @keyframes blinker {
        50% {
            opacity: 0;
     }
    }
    
   
    transform: rotate(-45deg);
    -webkit-transform: rotate(-45deg);

    
`