import React from 'react';
import {Container} from 'nes-react';
import {ClickableText, Title} from '../utils/'


function NavBar() {
    
    return (
      <div className="navbar">
        <Container dark = 'true'>
          <Title marginLeft = '-4%' >Jenryk's Base</Title>
            <Container dark ='true'>
                <ClickableText hoverColor = 'magenta' href = '#sobre'>Sobre</ClickableText>
                <ClickableText hoverColor = 'green' href = '#projetos'>Projetos</ClickableText>
                <ClickableText href = '#contatos'>Contatos</ClickableText>
            </Container>
        </Container>
      </div>
    );
  }
  
  export default NavBar;
  