import React from 'react'
import {Container} from 'nes-react'
import {ResponsiveDivI, ClickableText} from './utils'
import Bg from '../assets/pixel-art.jpg'
function Projects() {
    const projects = [{
        'title' : "Este site",
        'description' : "Este site foi um projeto simples para aprender os fundamentos do react e desenvolvimento de sites simples, este site foi construído com o kit nes.css, react-scrollable-anchors e styled components" ,
        'imgSrc' : '../assets/pixel-art.jpg',
        'link': 'jenryk.github.io/portfolio'
        }]
    const projectsElements = projects.map((project) => 
    <div>
        <Container dark = 'true' style = {{background : '#212529', color: 'light-grey'}} centered = 'true'>
            
            <ResponsiveDivI>
            <img src = {Bg} style ={{width: '50%', height: '50%'}} alt = 'bg'></img>
            <div style = {{marginLeft : "2%"}}>
            <ClickableText hoverColor = 'blue' style = {{marginBottom : '2%', fontSize: '2.3rem', color: '#3e8e75'}} href = {project.link} >{project.title}</ClickableText>
            <h4>{project.description}</h4>
            </div>
            </ResponsiveDivI>
        </Container>
    </div>
    )
    return(
        <Container title = 'Projects' dark = 'true' >
            {projectsElements}
            <h3 style = {{color : 'gray'}}>Ops... Acho que essa página é o meu único projeto atual :b</h3>
        </Container>
    )
}

export default Projects;