import React from 'react'
import {Container, Avatar} from 'nes-react'
import AvatarImage from '../assets/avatar.jpg'
import {ResponsiveDiv} from './utils'


function Top() {
    return(
        <Container  dark = 'true' title = 'Sobre' alignContent = 'center'>
            
            
            <ResponsiveDiv >
                
                    <Avatar rounded = 'true' large = 'true' style = {{width : '30vh' , height : '30vh'}} src = {AvatarImage}/>
                    <p style = {{}}>Olá, me chamo João Henrique e atualmente estou no caminho auto-didata para aprender desenvolvimento de front-end</p>    
      
            </ResponsiveDiv>      
        </Container>
    )

}

export default Top;